body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-open-sans: "Open Sans";
  --font-inter: Inter;
  --font-franklin-gothic: "Franklin Gothic";

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-mini: 15px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-gainsboro-100: rgba(217, 217, 217, 0);

  /* Border radiuses */
  --br-3xs: 10px;
  --br-xl: 20px;
}
